<template>
    <ion-page>
        <ion-header>
            <ion-toolbar>
            <ion-title>{{ title }}</ion-title>
            </ion-toolbar>
        </ion-header>
        <ion-content class="ion-padding">
            <ion-input placeholder="Enter password" v-model="pass"></ion-input>
            <a :href="'tabs/takequiz/'+id+'?pass='+pass">Begin</a><br />
            <a :href="'tabs/quizzes'">Close</a>
        </ion-content>
    </ion-page>
</template>

<script>
import { IonPage, IonContent, IonHeader, IonTitle, IonToolbar, IonInput } from '@ionic/vue';
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'Modal',
  props: {
    title: { type: String, default: 'Take quiz' },
    id: { type: Number }
  },
  data() {
    return {
      pass: ''
    }
  },
  components: { IonPage, IonContent, IonHeader, IonTitle, IonToolbar, IonInput }
});
</script>