<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-title>Quizzes</ion-title>
        <ion-input placeholder="Filter" @ionChange="handleFilter" v-model="filter"></ion-input>
        <a v-if="isTeacher" :href="'tabs/quiz/new'">Create quiz</a>
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-header collapse="condense">
        <ion-toolbar>
          <ion-title size="large">Quizzes</ion-title>
        </ion-toolbar>
      </ion-header>
      <ion-list>
        <ion-item v-for="item in items" :key="item">
          <ion-card>
            <ion-card-header>
                <ion-card-subtitle>{{item.name}}</ion-card-subtitle>
                <ion-card-title>{{item.description}}</ion-card-title>
                <ion-card-content>
                    <a v-if="isTeacher" :href="'tabs/quiz/'+item.id">Open quiz</a>
                    <ion-button v-if="!isTeacher" @click="takeQuiz(item.id)">Take quiz</ion-button>
                </ion-card-content>
            </ion-card-header>
        </ion-card>
        </ion-item>
      </ion-list>

      <ion-infinite-scroll
        @ionInfinite="loadData($event)" 
        threshold="100px" 
        id="infinite-scroll"
        :disabled="isDisabled"
      >
        <ion-infinite-scroll-content
          loading-spinner="bubbles"
          loading-text="Loading more data...">
        </ion-infinite-scroll-content>
      </ion-infinite-scroll>
    </ion-content>
  </ion-page>
</template>

<script>
import { modalController, IonButton, IonPage, IonHeader, IonToolbar, IonTitle, IonContent, IonInfiniteScroll, IonInfiniteScrollContent, IonItem, IonList, IonInput, IonCard, IonCardSubtitle, IonCardTitle, IonCardHeader, IonCardContent } from '@ionic/vue';
import axios from 'axios';
import { ref } from 'vue';
import TakeQuiz from '@/components/modals/TakeQuiz.vue'

export default  {
  name: 'Tab1',
  components: { IonButton, IonHeader, IonToolbar, IonTitle, IonContent, IonPage,  IonInfiniteScroll, IonInfiniteScrollContent, IonItem, IonList, IonInput, IonCard, IonCardSubtitle, IonCardTitle, IonCardHeader, IonCardContent },
  setup() {
    const isDisabled = ref(false);
    const toggleInfiniteScroll = () => {
      isDisabled.value = !isDisabled.value;
    }

    const loadData = () => {
      isDisabled.value = true;
    }

    return {
      isDisabled,
      toggleInfiniteScroll,
      loadData
    }
  },
  methods: {
     async handleFilter() {
        this.items.forEach(x => {
          if (x.name.toLowerCase().indexOf(this.filter.toLowerCase()) != -1 || x.description.toLowerCase().indexOf(this.filter.toLowerCase()) != -1){
            x.displayed = true;
          }else{
            x.displayed = false;
          }
        });
      },
      async takeQuiz(id){
        const modal = await modalController
          .create({
            component: TakeQuiz,
            componentProps: {
              id: id
            },
          })
        return modal.present();
      }
  },
  data: () => ({
    items: [],
    filter: '',
    isTeacher: window.localStorage.getItem('teacherJwt')
  }),
  created: function(){
    axios.get(process.env.VUE_APP_TEACHER_API_URL+'/quizmanagement/quizzes').then((response) => {
      response.data.forEach(element => {
        const item = {};
        item.name = element.name;
        item.description = element.description;
        item.id = element.id;
        item.displayed = true;
        this.items.push(item);
      });
    });
  },
}
</script>